.App {
  text-align: center;
}


body {
  background-color: #880e4f;
  padding-bottom: 60px;
  font-size: 16px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.search-icon{
  color: black
}
.App-link {
  color: #61dafb;
}
.collection-item {
  margin-bottom: 50px;
  margin-right: 50px;
  height: auto;
  border-radius: 10px;
  text-align: center;
  line-height: 150px;


  
}
.img-col {
  float: left;
  height: 100%;  

}
.desc-col {
  display: inline;
  width: 100px;
  height: 100%;
}
.price-col {
float: right;
 
  
}
.amount-selectors {
  display: inline-block;
  margin: 2px;
}
.showButton {
  background: none;
  border: none;
  font-weight: bold;
  outline: none;
}
.collection {
  border-color: #880e4f;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px
}
.input-field [type="search"]{
  border-radius: 100px;
}


.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 80px;
  line-height: 0%;
  opacity: 0.9;
  
}

.footer-a{
  height: 80px;
}